import { DocumentWidget } from '@jupyterlab/docregistry';
import * as React from 'react';
import { ReactWidget } from '@jupyterlab/apputils';
import { Signal } from '@lumino/signaling';
import { MainView } from './mainview';
export class JupyterViewWidget extends DocumentWidget {
    constructor(options) {
        super(options);
        this.onResize = (msg) => {
            window.dispatchEvent(new Event('resize'));
        };
    }
    /**
     * Dispose of the resources held by the widget.
     */
    dispose() {
        this.content.dispose();
        super.dispose();
        setTimeout(() => window.dispatchEvent(new Event('resize')), 100);
    }
}
export class JupyterViewPanel extends ReactWidget {
    /**
     * Construct a `ExamplePanel`.
     *
     * @param context - The documents context.
     */
    constructor(context) {
        super();
        this.addClass('jp-jupyterview-panel');
        this._context = context;
    }
    /**
     * Dispose of the resources held by the widget.
     */
    dispose() {
        if (this.isDisposed) {
            return;
        }
        Signal.clearData(this);
        super.dispose();
    }
    render() {
        return React.createElement(MainView, { context: this._context });
    }
}
